import { Component } from '@angular/core';
import { ModalController, ActionSheetController  ,AlertController, IonicPage,NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-helpview',
  templateUrl: 'helpview.html',
})
export class HelpviewPage {


  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  permisom: any=[];
    generated = '';
    public hasFilter: boolean = false;

  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor(public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,  public navParams: NavParams,   public regService: Auth
    //, private qrScanner: QRScanner

    ) {


  }


  public clearSearchg(): void {
    this.gSearchQuery = '';
  }






  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getRegsintok(  'help/5cf53eb0c1f7bc37bc919753/' + this.navParams.data.idempresa    ,'datosfijos').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }
  ionViewDidLoad() {
    this.pideregistros();
  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }



   participaRecord(item : any) : void
   {
    var data2 = {  html: item.texto,record : null,titulo:'Manual',formulario:'123',seguimiento:'0'} ;
    var modalPage = this.modalCtrl.create('CursosviewHtmlPage',data2,{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    modalPage.present();


     // this.navCtrl.push('CursostabViewPage', { record : item,idt:item._id,idempresa:this.navParams.data.idempresa,idplantilla:this.navParams.data.idempresa,idunidad:this.navParams.data.idunidad,idmodulo:item._id,nombrep:item.nombre,html:item.contenido });
   }

}



